/* Fuentes */
@font-face {
    font-family: 'Old English Text MT';
    src: url('https://fonts.gstatic.com/s/oldenglishtextmt/v12/4yA6ZXgBsKfD3aSjN5AETPyAcg.ttf') format('truetype');
  }
  
  /* Colores */
  :root {
    --color-primario: #c62828;
    --color-secundario: #4a148c;
    --color-fondo: #f5f5f5;
    --color-texto: #212121;
  }
  
  /* Estilos generales */
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  img {
    -webkit-user-drag: none; /* para Safari */
    -khtml-user-drag: none; /* para Konqueror */
    -moz-user-drag: none; /* para Firefox */
    -o-user-drag: none; /* para Opera */
  }
  
  
  body {
    font-family: 'Old English Text MT', 'Arial', sans-serif;
    font-size: 1em;
    color: var(--color-texto);
    background: linear-gradient(to bottom right, var(--color-primario), var(--color-secundario));
  }
  
  nav {
    background: linear-gradient(to bottom, #ff4081, #f48fb1);
    border-bottom: 0.5em solid var(--color-primario);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em 2em;
    border-radius: 0.5em;
  }

  nav a {
    color: var(--color-texto);
    text-decoration: none;
    font-size: 1.2em;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  
  nav a:hover {
    transform: scale(1.1);
    color: var(--color-secundario);
  }
  
  .logo img {
    max-width: 4.25em; /* 52px */
  }
  .miel {
    width: 20%;
    max-width: 4em;
    height: 4em;
    margin-left: auto;
    margin-right: 1em;
    animation-name: rotate;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
  
  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .connect-wallet button {
    background: linear-gradient(to right, #f9d9e2, #c2e59c, #a8e6cf, #f3c7c3);
    color: white;
    height: 3.4em;
    border: none;
    border-radius: 0.75em; /* 12px */
    padding: 0.625em 2.5em; /* 10px 40px */
    font-size: 1em; /* 16px */
    transition: all 0.3s ease;
    margin: 0.3em 0em 0em 0em;
    background-size: 400% 400%;
    animation: gradient 8s ease infinite;
  }
  
  .connect-wallet button:hover {
    background-position: right center;
    cursor: pointer;
  }
  
  .connect-wallet button:focus {
    outline: none;
  }

  @keyframes gradient {
      0% {
          background-position: 0% 50%;
      }
      50% {
          background-position: 100% 50%;
      }
      100% {
          background-position: 0% 50%;
      }
  }
  /*balances */
 .container {
    background: linear-gradient(to bottom, #ffe0b2, #ffcc80, #ffb74d, #ffa726, #ff9800);
    margin: 3em auto;
    padding: 2em;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    overflow: hidden;
    font-size: medium;
    border-radius: 2em;
    max-width: 50em;
    text-align: center;
    box-shadow: 0 0.5em 1em rgba(0, 0, 0, 0.2);
  }
  /*efecto pantalla*/
  .Pantalla {
    display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  }

  .Pantalla img {
    width: 100%;
    max-width: 400px;
    margin: auto;
  }
  

  /*fromconteiner*/
  .form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2em;
  }
  
  .input {
    font-size: 1.25em;
    padding: 0.75em 1.5em;
    border: none;
    border-radius: 1.25em;
    margin-bottom: 0.75em;
    text-align: center;
    background-color: #fff;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    -webkit-appearance: none; /* Para Safari y Chrome */
    -moz-appearance: none; /* Para Firefox */
    appearance: none; /* Para otros navegadores */
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
  
  .input:focus {
    outline: none;
  }
  
  .min-max-text {
    font-size: 0.9em;
    color: #666;
  }
  
  .input:focus + .min-max-text {
    animation: shake 0.8s;
  }
  
  @keyframes shake {
    0% {
      transform: translateX(0);
    }
    20% {
      transform: translateX(-0.5em);
    }
    40% {
      transform: translateX(0.5em);
    }
    60% {
      transform: translateX(-0.5em);
    }
    80% {
      transform: translateX(0.5em);
    }
    100% {
      transform: translateX(0);
    }
  }
  .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.2em;
    color: #fff;
  }
  .bolsaD{
    max-width: 1.4em;
    height: 1.4em;
    margin:auto;
  }
  .balance {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1em;
  }
  
  .balance-contract {
    margin-bottom: 0.5em;
    background-color: #f6e58d;
    padding: 0.5em 1em;
    border-radius: 5px;
  }
  
  .rewards-contract {
    margin-bottom: 0.5em;
    background-color: #ffbe76;
    padding: 0.5em 1em;
    border-radius: 5px;
  }
  
  .hasInvested-contract {
    margin-bottom: 0.5em;
    background-color: #ff7979;
    padding: 0.5em 1em;
    border-radius: 5px;
  }
  
  .bnbTvl-contract {
    margin-bottom: 0.5em;
    background-color: #badc58;
    padding: 0.5em 1em;
    border-radius: 5px;
  }
  
  .dailyReward-contract {
    margin-top: 1em;
    color: #f6e58d;
  }
  
  /* Animaciones */
  .balance-contract:hover,
  .rewards-contract:hover,
  .hasInvested-contract:hover,
  .bnbTvl-contract:hover,
  .dailyReward-contract:hover {
    animation-name: pulse0;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
  
  @keyframes pulse0 {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
    /*Botones*/
    .button-container {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 2em;
      }
      
      .staking button,
      .claim button,
      .withdraw button {
        color: #fff;
        background-color: #fce46e;
        border: none;
        padding: 0.5em 1em;
        border-radius: 5px;
        font-size: 1.2em;
        margin: 0 1em;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
      }
      
      .staking button:hover,
      .claim button:hover,
      .withdraw button:hover {
        background-color: #ffbe76;
        transform: scale(1.1);
      }
      
      .staking button:focus,
      .claim button:focus,
      .withdraw button:focus {
        outline: none;
      }
      
      .staking button:active,
      .claim button:active,
      .withdraw button:active {
        background-color: #ffbe76;
        transform: translateY(2px);
      }
      
      /* Animaciones */
      .staking button:hover,
      .claim button:hover,
      .withdraw button:hover {
        animation-name: pulse;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
      
      @keyframes pulse {
        0% {
          transform: scale(1);
        }
        50% {
          transform: scale(1.2);
        }
        100% {
          transform: scale(1);
        }
      }
      /*pinicio*/
      .pinicio {
        color: #fff;
        font-size: 2em;
        text-align: center;
        margin-top: 3em;
        font-family: fantasy;
      }
      
      .pinicio.hide {
        display: none;
      }
      .hide {
        display: none;
      }
      /* Animaciones */
      .pinicio {
        animation-name: shine;
        animation-duration: 2s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
      
      @keyframes shine {
        0% {
          color: #fff;
          text-shadow: none;
        }
        20% {
          color: #b0e0e6;
          text-shadow: 0 0 5px #b0e0e6, 0 0 10px #b0e0e6, 0 0 15px #b0e0e6,
        }
        50% {
          color: #fff;
          text-shadow: none;
        }
      }
      
