  .brujaparners {
    display: flex;
    justify-content: center;
    margin-top: 1em;
    position: relative;
    font-size: 1.2em;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    text-align: center;
    border: 2px solid #ff6680; /* borde de 2px con color morado */
    border-radius: 5px; /* esquinas redondeadas */
    padding: 0.5em 1em; /* relleno interno */
    background-color: #dfe6e9; /* fondo gris claro */
  }
  
  .brujaparners img {
    margin-right: 0.5em;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .Polygator,
  .BearsMiners,
  .coinminerpro,
  .bnbroi,
  .Maticminer,
  .flashloanbnb {
    display: inline-block;
    width: 8em;
    height: 8em;
    margin: 1em;
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  
  .Polygator img,
  .BearsMiners img,
  .coinminerpro img,
  .Maticminer img,
  .bnbroi img,
  .flashloanbnb img {
    position: absolute;
    top: 0;
    left: 0;
    margin: 1em;
    width: 70%;
    height: 70%;
    opacity: 0;
    transition: all 0.5s ease;
  }
  
  .Polygator:hover img,
  .BearsMiners:hover img,
  .coinminerpro:hover img,
  .Maticminer:hover img,
  .bnbroi:hover img,
  .flashloanbnb:hover img {
    opacity: 1;
    transform: scale(1.1);
  }
  
  .Polygator::before,
  .Maticminer::before,
  .BearsMiners::before,
  .coinminerpro::before,
  .bnbroi::before,
  .flashloanbnb::before {
    content: '';
    position: absolute;
    top: -100%;
    left: 50%;
    transform: translateX(-50%);
    width: 150%;
    height: 150%;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    animation: pulse1 1s ease-in-out infinite;
  }
  
  @keyframes pulse1 {
    0% {
      transform: translateX(-50%) scale(0.2);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: translateX(-50%) scale(1.5);
      opacity: 0;
    }
  }
  
  .Polygator:hover::before,
  .BearsMiners:hover::before,
  .coinminerpro:hover::before,
  .bnbroi:hover::before,
  .Maticminer:hover::before,
  .flashloanbnb:hover::before {
    animation: none;
  }
  
